			"name": "Ayurvedic Baby Massage Oil with Cow ghee, Turmeric, Sesame oil - 200 ml",
			"hindi_name": "      (, ,       ) - 200 ml",
			"description": "<p style=\"text-align:start\"><span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">Daily massage is a great way to establish a deep bond between mother and baby. A classic Ayurvedic formulation, known as Lakshadi Kuzhambu, Mylo Care&rsquo;s Ayurvedic Baby Massage Oil is enriched with Ashwagandha, Cow Ghee, Turmeric, Curd Water and 9 other herbs. Daily massage with this oil helps nourish and soothe baby&rsquo;s delicate skin and aids in development and strengthening of bones and muscles.</span></span></span></p>\r\n\r\n<p style=\"text-align:start\"><span style=\"color:#222222\"><span style=\"background-color:#ffffff\">&nbsp;</span></span></p>\r\n\r\n<p style=\"text-align:start\"><span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">An all-natural oil without any harsh chemicals or artificial fragrance, when used daily, it helps to build baby&rsquo;s immunity and a clear complexion.</span></span></span></p>\r\n\r\n<p style=\"text-align:start\"><br />\r\n&nbsp;</p>\r\n\r\n<p style=\"text-align:start\"><strong><span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">Benefits</span></span></span></strong></p>\r\n\r\n<p style=\"text-align:start\"><span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">&nbsp; &nbsp; &middot;&nbsp;</span><span style=\"color:black\">Strengthens bones &amp; muscles</span></span></span><br />\r\n<span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">&nbsp; &nbsp; &middot;&nbsp;</span><span style=\"color:black\">Increases Immunity, fights infections</span></span></span><br />\r\n<span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">&nbsp; &nbsp; &middot;&nbsp;</span><span style=\"color:black\">Clears complexion</span></span></span><br />\r\n<span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">&nbsp; &nbsp; &middot;&nbsp;</span><span style=\"color:black\">Soothes&nbsp;&amp; Nourishes</span></span></span><br />\r\n<span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">&nbsp; &nbsp; &middot;&nbsp;</span><span style=\"color:black\">Promotes healthy hair growth&nbsp;</span></span></span></p>\r\n\r\n<p style=\"text-align:start\">&nbsp;</p>\r\n\r\n<p style=\"text-align:start\"><strong><span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">How to use:</span></span></span></strong><br />\r\n<span style=\"color:#222222\"><span style=\"background-color:#ffffff\">&nbsp; &nbsp; &middot;&nbsp;<span style=\"color:black\">Pour oil between your palms and rub together to warm the oil</span></span></span><br />\r\n<span style=\"color:#222222\"><span style=\"background-color:#ffffff\">&nbsp; &nbsp; &middot;&nbsp;<span style=\"color:black\">Massage gently on baby hair, face and body&nbsp;</span></span></span><br />\r\n<span style=\"color:#222222\"><span style=\"background-color:#ffffff\">&nbsp; &nbsp; &middot;&nbsp;<span style=\"color:black\">Leave it on for 15 minutes</span></span></span><br />\r\n<span style=\"color:#222222\"><span style=\"background-color:#ffffff\">&nbsp; &nbsp; &middot;&nbsp;<span style=\"color:black\">Cleanse with a mild soap. Apply daily for best use</span></span></span></p>\r\n\r\n<p style=\"text-align:start\">&nbsp;</p>\r\n\r\n<p style=\"text-align:start\"><strong><span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">Note:</span></span></span></strong></p>\r\n\r\n<p style=\"text-align:start\"><span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"color:black\">For external use only. Avoid contact with eyes, ears and mouth. If contact occurs, rinse well with water.</span></span></span></p>\r\n\r\n<p style=\"text-align:start\"><span style=\"color:#222222\"><span style=\"background-color:#ffffff\">&nbsp;</span></span></p>\r\n\r\n<p style=\"text-align:start\"><strong><span style=\"color:#222222\"><span style=\"background-color:#ffffff\">Ingredients:</span></span></strong></p>\r\n\r\n<p style=\"text-align:start\"><span style=\"color:#222222\"><span style=\"background-color:#ffffff\"><span style=\"background-color:#f9f9f9\"><span style=\"color:#333333\">Aswagandha (Withania somnifera</span></span>),&nbsp;<span style=\"color:#333333\">Nisha (Curcuma longa),&nbsp;</span><span style=\"background-color:#f9f9f9\"><span style=\"color:#333333\">Dharu (Cedrus deodara),&nbsp;</span></span><span style=\"color:#333333\">Mustha (Cyperus rotundus</span>),&nbsp;<span style=\"background-color:#f9f9f9\"><span style=\"color:#333333\">Moorva (Marsdenia tenacissima</span></span>),&nbsp;<span style=\"color:#333333\">Rohini (Picrorhiza kurroa</span>),&nbsp;<span style=\"background-color:#f9f9f9\"><span style=\"color:#333333\">Rasna (Alpinia galangal),&nbsp;</span></span><span style=\"color:#333333\">Yashtimadhu (Glycyrrhiza glabra),&nbsp;</span><span style=\"background-color:#f9f9f9\"><span style=\"color:#333333\">Laksha (Laccifer lacca),&nbsp;</span></span><span style=\"color:#333333\">Masthu (Yogurt/Curd-water),&nbsp;</span><span style=\"background-color:#f9f9f9\"><span style=\"color:#333333\">Thailam (Sesame oil),&nbsp;</span></span><span style=\"color:#333333\">Gritham (Ghee),&nbsp;</span><span style=\"background-color:#f9f9f9\"><span style=\"color:#333333\">Eranda Thailam (Castor oil)</span></span></span></span></p>\r\n",
