			"description": "<p><strong>Description </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>Made from high-grade coconuts, Mylo Care&rsquo;s Cold Pressed Extra Pure Coconut Oil is a versatile massage oil for babies and expecting/new mothers. Ideal for all skin and hair types, this oil is rich in Vitamin E. Because it is Extra Pure, Cold-Pressed and 100% natural, it retains its unique balance of nutrients, making it ideal for babies&rsquo; delicate skin.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Benefits </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ol>\r\n\t<li>\r\n\t<p><strong>For the baby:</strong></p>\r\n\t</li>\r\n</ol>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Massage - </strong>The ultra-hydrating properties of coconut oil make it the best choice for massaging babies.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Nappy rash - </strong>Its anti-bacterial and anti-inflammatory properties make it ideal to use as a nappy rash solution.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Removes cradle cap - </strong>Scaly patches on the scalp called cradle cap are common in babies and coconut oil helps to loosen them with ease.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Mosquito bites - </strong>Provides relief from itching, fades scars, and speeds up healing.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Dry skin - </strong>Gets absorbed in the skin readily and is a great remedy for dry skin, eczema, and chapped lips.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Nourishes hair - </strong>Works great as a hair conditioner and helps to strengthen baby hair.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Cures baby acne - </strong>Possesses antimicrobial properties and helps to get rid of acne.</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ol start=\"2\">\r\n\t<li>\r\n\t<p><strong>For mother</strong><strong>&nbsp;</strong></p>\r\n\t</li>\r\n</ol>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Heals and moisturizes the skin - </strong>Helps hydrate the skin and retain its moisture.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Helps fade &amp; repair stretch marks - </strong>Nourishes the skin and helps repair stretch marks.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Nourishes hair - </strong>Helps to make the hair follicles stronger and reduces hair breakage.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Treats dandruff - </strong>Vitamin E and K present in the coconut oil help to get rid of dandruff.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Soothes sore nipples - </strong>Its healing properties help to soothe sore and cracked nipples.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Improves the quality of breastmilk - </strong>The antimicrobial fats present in it help to boost the baby&rsquo;s immune system.</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Usage: </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>To be used as a massage oil by expectant and new mothers and babies. Due to its unadulterated nature, the oil may solidify. To liquefy, place a tightly sealed bottle in warm water.</p>\r\n",
