			"description": "<p><u><strong>DHANWANTARAM OIL</strong></u></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Description </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>Mylo Care&rsquo;s Dhanwantaram Oil is a highly effective massage oil that is prescribed in Ayurveda for pre and postnatal massage. Made from highly effective ingredients such as Sesame, Sandalwood, Ashwagandha, Shatavari, Amla and Cow&rsquo;s Milk, this oil has many benefits for expecting and new mothers.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Benefits</strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Pain Relief: </strong>Helps relieve muscle cramps, inflammation, joint stiffness, and lower back pain</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Rejuvenates Body: </strong>Pre-delivery massage strengthens muscles around the pelvic area and improves skin elasticity which helps in facilitating delivery. Post-delivery massage helps to firm up the sagging skin&nbsp;</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Stretch Marks: </strong>Regular massage helps prevent stretch marks and dry skin</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>How To Use</strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Pre-delivery:</strong> Recommended to be used from the 7th month of pregnancy.&nbsp;Massage for 10-15 mins around the stomach and lower back. Leave it for 30 mins and wash it off with a mild soap</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Post-delivery: </strong>Use it after 1 week in case of normal delivery and after 2 weeks in case of c-section delivery.&nbsp;Massage for 10-15 mins around the stomach and lower back. Leave it on for 30 mins and wash it off with a mild soap.</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>To be used externally only by expecting and new mothers. Not be used on babies. This is body oil. Not meant for facial and hair usage.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Ingredients </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Sesame: </strong>Commonly known as Til ka tail, helps reduce inflammation and body pain</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Sandalwood: </strong>Helps relieve anxiety and prevent skin disorders</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Ashwagandha: </strong>Helps improve blood circulation and energy levels&nbsp;</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Shatavari: </strong>With natural antioxidant and anti-inflammatory benefits. Shatavari helps build immunity</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Amla: </strong>It is rich in Vitamin C which helps in brightening, softening and firming up of skin&nbsp;</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>NOTE: THIS OIL HAS A CHARACTERISTIC AYURVEDIC OIL AROMA. SOME USERS MAY FIND IT PUNGENT SMELLING BUT THAT IS BECAUSE OF THE USE OF ONLY PURE INGREDIENTS. THIS IS A 100% NATURAL OIL AND NO HARMFUL CHEMICALS OR ARTIFICIAL FRAGRANCES ARE ADDED TO IT.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><u><strong>COLD PRESSED EXTRA VIRGIN COCONUT OIL&nbsp;</strong></u></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Description </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>Made from high-grade coconuts, Mylo Care&rsquo;s Cold Pressed Extra Virgin Coconut Oil is a versatile massage oil for babies and expecting/new mothers. Ideal for all skin and hair types, this oil is rich in Vitamin E. Because it is Extra Virgin, Cold-Pressed and 100% natural, it retains its unique balance of nutrients, making it ideal for babies&rsquo; delicate skin.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Benefits </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ol>\r\n\t<li>\r\n\t<p><strong>For the baby:</strong></p>\r\n\t</li>\r\n</ol>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Massage - </strong>The ultra-hydrating properties of coconut oil make it the best choice for massaging babies.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Nappy rash - </strong>Its anti-bacterial and anti-inflammatory properties make it ideal to use as a nappy rash solution.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Removes cradle cap - </strong>Scaly patches on the scalp called cradle cap are common in babies and coconut oil helps to loosen them with ease.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Mosquito bites - </strong>Provides relief from itching, fades scars, and speeds up healing.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Dry skin - </strong>Gets absorbed in the skin readily and is a great remedy for dry skin, eczema, and chapped lips.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Nourishes hair - </strong>Works great as a hair conditioner and helps to strengthen baby hair.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Cures baby acne - </strong>Possesses antimicrobial properties and helps to get rid of acne.</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ol start=\"2\">\r\n\t<li>\r\n\t<p><strong>For mother&nbsp;</strong></p>\r\n\t</li>\r\n</ol>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Heals and moisturizes the skin - </strong>Helps hydrate the skin and retain its moisture.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Helps fade &amp; repair stretch marks - </strong>Nourishes the skin and helps repair stretch marks.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Nourishes hair - </strong>Helps to make the hair follicles stronger and reduces hair breakage.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Treats dandruff - </strong>Vitamin E and K present in the coconut oil help to get rid of dandruff.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Soothes sore nipples - </strong>Its healing properties help to soothe sore and cracked nipples.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Improves the quality of breastmilk - </strong>The antimicrobial fats present in it help to boost the baby&rsquo;s immune system.</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Usage: </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>To be used as a massage oil by expectant and new mothers and babies. Due to its unadulterated nature, the oil may solidify. To liquefy, place a tightly sealed bottle in warm water.</p>\r\n",
