			"description": "<p><strong>Cotton Ultra Soft Pregnancy Pillow- Description</strong></p>\r\n\r\n<p>This pregnancy pillow is a full-body solution for pregnant mothers, that replaces a whole array of pillows, and allows you to snuggle up to just one. Its shape is designed to give comfort to the complete body. The upper part of the pillow is designed to give maximum support to your head and neck. The middle part can be used as back support or to support your tummy. While the lower part is for between the knees. The pillow helps you in preventing back pain and temperature control and props up your head to aid in breathing and helps to prevent heartburn and other pregnancy pitfalls.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Features &amp; details</strong></p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p>Turns into a mini lounger for you, ideal for reading or watching TV</p>\r\n\t</li>\r\n\t<li>\r\n\t<p>Keeps away common allergies such as sneezing, wheezing, etc.</p>\r\n\t</li>\r\n\t<li>\r\n\t<p>Gives aligned support to the neck, spine, hips, and legs&nbsp;</p>\r\n\t</li>\r\n\t<li>\r\n\t<p>Provides ultimate cushioned support to the lower body</p>\r\n\t</li>\r\n\t<li>\r\n\t<p>Quickly restores to its original position after use&nbsp;</p>\r\n\t</li>\r\n\t<li>\r\n\t<p>Designed to give comfort to the complete body</p>\r\n\t</li>\r\n\t<li>\r\n\t<p>Fluffy and lightweight with zero pressure</p>\r\n\t</li>\r\n\t<li>\r\n\t<p>Can also be used as a feeding pillow&nbsp;</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Product information</strong></p>\r\n\r\n<table>\r\n\t<tbody>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Type&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>Pregnancy pillow&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Shape&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>U-shape&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Usage&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>Indoor&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>No. of items&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>1</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Materials&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>Cover fabric: 100% cotton, with zipper</p>\r\n\r\n\t\t\t<p>Inner fabric: non-woven fabric&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Filling&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>PP cotton&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Washable&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>Yes&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Portable&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>No&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Item weight&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>2.5kg&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t\t<tr>\r\n\t\t\t<td>\r\n\t\t\t<p>Product dimensions&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t\t<td>\r\n\t\t\t<p>130cm X 70cm&nbsp;</p>\r\n\t\t\t</td>\r\n\t\t</tr>\r\n\t</tbody>\r\n</table>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Ayurvedic Bio Therapy Oil for Pregnancy Pain, Stretch Marks and Sagging Skin- Description </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>Mylo Care&rsquo;s Dhanwantaram Oil is a highly effective massage oil that is prescribed in Ayurveda for pre and postnatal massage. Made from highly effective ingredients such as Sesame, Sandalwood, Ashwagandha, Shatavari, Amla and Cow&rsquo;s Milk, this oil has many benefits for expecting and new mothers.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Benefits</strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Pain Relief: </strong>Helps relieve muscle cramps, inflammation, joint stiffness, and lower back pain</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Rejuvenates Body: </strong>Pre-delivery massage strengthens muscles around the pelvic area and improves skin elasticity which helps in facilitating delivery. Post-delivery massage helps to firm up the sagging skin&nbsp;</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Stretch Marks: </strong>Regular massage helps prevent stretch marks and dry skin</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>How To Use</strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Pre-delivery:</strong> Recommended to be used from the 7th month of pregnancy.&nbsp;Massage for 10-15 mins around the stomach and lower back. Leave it for 30 mins and wash it off with a mild soap</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Post-delivery: </strong>Use it after 1 week in case of normal delivery and after 2 weeks in case of c-section delivery.&nbsp;Massage for 10-15 mins around the stomach and lower back. Leave it on for 30 mins and wash it off with a mild soap.</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>To be used externally only by expecting and new mothers. Not be used on babies. This is body oil. Not meant for facial and hair usage.</p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p><strong>Ingredients </strong></p>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<ul>\r\n\t<li>\r\n\t<p><strong>Sesame: </strong>Commonly known as Til ka tail, helps reduce inflammation and body pain</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Sandalwood: </strong>Helps relieve anxiety and prevent skin disorders</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Ashwagandha: </strong>Helps improve blood circulation and energy levels&nbsp;</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Shatavari: </strong>With natural antioxidant and anti-inflammatory benefits. Shatavari helps build immunity</p>\r\n\t</li>\r\n\t<li>\r\n\t<p><strong>Amla: </strong>It is rich in Vitamin C which helps in brightening, softening and firming up of skin&nbsp;</p>\r\n\t</li>\r\n</ul>\r\n\r\n<p>&nbsp;</p>\r\n\r\n<p>NOTE: THIS OIL HAS A CHARACTERISTIC AYURVEDIC OIL AROMA. SOME USERS MAY FIND IT PUNGENT SMELLING BUT THAT IS BECAUSE OF THE USE OF ONLY PURE INGREDIENTS. THIS IS A 100% NATURAL OIL AND NO HARMFUL CHEMICALS OR ARTIFICIAL FRAGRANCES ARE ADDED TO IT.</p>\r\n",
